.page {
  font-family: 'Roboto', sans-serif;
  font-weight: normal;
  font-size: 16px;
  line-height: 27px;
  color: $color-gray;
  scroll-behavior: smooth;

  &__body {
    margin: 0;
  }

  &__body--with-menu {
    overflow: hidden;
  }

  &__menu {
    position: fixed;
    top: 0;
    left: 0;
    right: 0;

    transform: translateX(-100%);
    transition: transform 0.3s;

    &:target {
      transform: translateX(0);
      z-index: 1;
    }
  }

  &__section {
    margin-bottom: 64px;

    @include onDesktop {
      margin-bottom: 104px;
    }

    &-title {
      margin: 0 0 32px;
      font-weight: bold;
      font-size: 36px;
      line-height: 36px;
      text-align: center;
      letter-spacing: -1.5px;
      color: $color-black;

      @include onDesktop {
        margin-bottom: 56px;
        font-size: 48px;
        line-height: 48px;
      }

      &::after {
        content: "";
        background-image: url(../images/crown.svg);
        background-position: center;
        background-size: contain;
        background-repeat: no-repeat;
        margin: 4px auto 0;
        display: block;
        width: 23px;
        height: 16px;
      }
    }
  }
}
